@import './var.scss';
@import "./theme";
.tempalte-pageTab-nav {
  display: flex;
  overflow: hidden;
  border-radius: 30px 30px 0 0;
  align-items: flex-end;
  .tempalte-pageTab-nav-item {
    position: relative;
    display: flex;
    flex: 1;
    align-items: center;
    justify-content: center;
    height: 60px;
    font-size: 20px;
    font-family: PingFangSC-Semibold, PingFang SC;
    font-weight: 600;
    background: rgba(54, 45, 74, .4);
    color: rgba(255, 255, 255, 0.5);
    cursor: pointer;
    &:first-child {
      border-top-left-radius: 20px;
    }
    &:last-child {
      border-top-right-radius: 20px;
    }
  }
  .tempalte-pageTab-nav-active {
    background-color: $color-table-bg;
    color: rgba(255, 255, 255, 1);
    height: 70px;
    font-size: 30px;
    &::before {
      content: '';
      position: absolute;
      left: -70px;
      top: 0;
      width: 70px;
      height: 100%;
      background: url(../images/common_tab_left.png) no-repeat;
      background-size: 100% 100%;
    }
    &::after {
      z-index: 2;
      content: '';
      position: absolute;
      right: -70px;
      top: 0;
      width: 70px;
      height: 100%;
      background: url(../images/common_tab_right.png) no-repeat;
      background-size: 100% 100%;
    }
  }
}

@include mobile-only {
  .tempalte-pageTab-nav {
    .tempalte-pageTab-nav-item {
      height: 35px;
      font-size: 14px;
    }

    .tempalte-pageTab-nav-active {
      height: 40px;
      font-size: 18px;
      &::before {
        left: -40px;
        width: 40px;
      }
      &::after {
        right: -40px;
        width: 40px;
      }
    }
  }
}
